import React from 'react';
import PropTypes from 'prop-types';
import NotificationContent from './notificationContent';
//import { Test } from './Notification.styles';

const Notification = (props) => (
  <li className="dropdown notification-dropdown">
    <a href="#!" className="dropdown-toggle nk-quick-nav-icon" data-toggle={props?.notifications && "dropdown"}>
      {props?.notifications ?
        (<div className="icon-status icon-status-info">
          <em className="icon ni ni-bell" />
        </div>)
        :
        (<em className="icon ni ni-bell-off" />)
      }
    </a>
    {props?.notifications &&
      (<div className="dropdown-menu dropdown-menu-xl dropdown-menu-right dropdown-menu-s1">
        <div className="dropdown-head">
          <span className="sub-title nk-dropdown-title">Notifications</span>
          <a href="#!">Mark All as Read</a>
        </div>
        <div className="dropdown-body">
          <div className="nk-notification">
            {
              props?.notifications?.map(function (notification,i) {
                /* TODO, change the key={i} to notification ID */
                return <NotificationContent key={i} color={notification.color} icon={notification.icon} text={notification.text} time={notification.time} />;
              })
            }
          </div>{/* .nk-notification */}
        </div>{/* .nk-dropdown-body */}
        {/* <div className="dropdown-foot center">
                <a href="#"}iew All</a>
              </div> */}
      </div>)}
  </li>
);

Notification.propTypes = {
  notifications: PropTypes.array,
};

Notification.defaultProps = {
  notifications:  [
    {
      text: "20 EAZIPRT reward available",
      time: '2hrs ago',
      icon: 'ni-check',
      color: 'success',//success, warning, info, error
    },
    {
      text: "100 EAZI staked on EAZIPRT",
      time: '4 hrs ago',
      icon: 'ni-curve-down-left',
      color: 'secondary',//success, warning, info, error
    },
    {
      text: "300 EAZI staked on EAZICRT",
      time: '6 hrs ago',
      icon: 'ni-curve-down-left',
      color: 'secondary',//success, warning, info, error
    },
    {
      text: "500 EAZI token acquired",
      time: '6 hrs ago',
      icon: 'ni-curve-down-right',
      color: 'info',//success, warning, info, error
    },
    {
      text: "Welcome to eaZI.finance",
      time: '9 hrs ago',
      icon: 'ni-heart-fill',
      color: 'success',//success, secondary, warning, info, error
    },
]
};

