import React, { useState } from 'react';
import PropTypes from 'prop-types';
//import { Test } from './ActivePlanSlider.styles';

const ActiveStakeSlider = (props) => {
  const [stake] = useState(props.stake);

  return <div className="slider-item">
    <div className="nk-iv-wg1">
      <div className="nk-iv-wg1-sub sub-text">Active Stakes</div>
      <h6 className="nk-iv-wg1-info title">{stake.title} - {stake.minInterest}% for {stake.matureAt}</h6>
      <a href="#!" className="nk-iv-wg1-link link link-light"><em className="icon ni ni-trend-up" /> <span>View Stake</span></a>
      <div className="nk-iv-wg1-progress">
        <div className="progress-bar bg-primary" data-progress={stake.percentCompleted} />
      </div>
    </div>
  </div>;
}

ActiveStakeSlider.propTypes = {
  stake: PropTypes.object,
};

ActiveStakeSlider.defaultProps = {
  stake: {},
};

export default ActiveStakeSlider;
