import React, { useState } from 'react';
import PropTypes from 'prop-types';

const TotalAmountElem = (props) => {
  const [amount] = useState(props.amount);
  const [title] = useState(props.title);
  const [change] = useState(props.change);

  return <div className="card-inner">
    <div className="nk-iv-wg2">
      <div className="nk-iv-wg2-title">
        <h6 className="title">{title} <em className="icon ni ni-info" /></h6>
      </div>
      <div className="nk-iv-wg2-text">
        <div className="nk-iv-wg2-amount">{amount.toLocaleString()} eaZI
          {change !== 0 && (change > 0 ?
            <span className="change up"><span className="sign" />{change}%</span>
            :
            <span className="change down"><span className="sign" />{change}%</span>
          )}
        </div>
      </div>
    </div>
  </div>
};

TotalAmountElem.propTypes = {
  title: PropTypes.string,
  amount: PropTypes.number,
  change: PropTypes.number,
};

TotalAmountElem.defaultProps = {
  title: '',
  amount: 0.00,
  change: 0.00,
};

export default TotalAmountElem;
